function CircularProgressWithLabel(props) {
  return (
    <Box position="relative" display="inline-flex">
      <CircularProgress size={63} variant="static"  {...props} />
      <Box
        top={0}
        left={0}
        bottom={0}
        right={0}
        position="absolute"
        display="flex"
        alignItems="center"
        justifyContent="center"
      >
        <Typography variant="caption" component="div" color="textSecondary">{`${Math.round(
          props.value,
        )}%`}</Typography>
      </Box>
    </Box>
  );
}
function UpdateWeek(){
    var data={
      week:localStorage.getItem("current")
    }
    axios({
    method:"PUT",
    url: `${process.env.REACT_APP_API_END}/businesses/updateWeek`,
    data:data,
    headers:{
      'x-auth-token':localStorage.getItem('token')
    }
  }
    )
    .then(res => console.log("done"))
}

function youtubeLinkParser(url) {
    var regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=|\?v=)([^#\&\?]*).*/;
    var match = url.match(regExp);
    if (match && match[2].length == 11) {
        return match[2];
    } else {
        return null;
    }
}

function Downloads(props){

     var Notes=props.data.Notes;
     var Task=props.data.Task;
    

     const handleClick = key => () =>{
                        var current =parseInt(localStorage.getItem("current"),10);
    var week=props.data.Week;


           var percent=0;
           if(week===current){
           switch (key) {
                 case "Notes":
                   localStorage.setItem("Notes",true);
                    if(parseInt(localStorage.getItem("notedown"),10) ===0){
                       percent =parseInt(localStorage.getItem("percent"),10)+33;
                       localStorage.setItem("percent", percent);
                       localStorage.setItem("notedown",1);

                     }
                   break;
                  case "Task":
                   localStorage.setItem("Task",true);
                   if(parseInt(localStorage.getItem("taskdown"),10) ===0){
                      var com =parseInt(localStorage.getItem("percent"),10)+33;
                       localStorage.setItem("percent", com);
                      localStorage.setItem("taskdown",1);

                     }
                   break;
                  default:
                   break;
                 }
             }
              var  check =parseInt(localStorage.getItem("percent"),10);
            if(check===100){
             var current =parseInt(localStorage.getItem("current"),10)+1;
             localStorage.setItem("current", current);
             localStorage.setItem("percent", 0);
               localStorage.setItem("watched", 0);
              localStorage.setItem("taskdown",0);
              localStorage.setItem("notedown",0);

             localStorage.setItem("Notes", false);
             localStorage.setItem("Task", false);
             localStorage.setItem("Videos", false);
             UpdateWeek();

       }
       };
    return(
            <div class='Notes'>
               <Grid container>
                  <Grid class='notestext' item xs={6}>
                       <Typography style={{'text-align':'center','font-size': '1.0em',marginLeft:"70px",marginTop:'11px'}}>Notes</Typography>
                      <Typography style={{'text-align':'center','font-size': '1.0em',marginTop:"29px"}}>Weekly Task</Typography> 
                 </Grid>
                 <Grid item xs={1}>
                  <div class='notesicon'>

                   <IconButton onClick={handleClick("Notes")} href={Notes} target="_blank">
                    <GetAppIcon  />
                    </IconButton>
                    <IconButton onClick={handleClick("Task")} href={Task} target="_blank">
                    <GetAppIcon />
                    </IconButton>
                  </div>
                </Grid>  
                
              </Grid>
            </div>

    )


}

class ContentPage extends React.Component{
   constructor(props) {

    super(props);



    this.state = {
      data:props.data,
      thumbnail:[],
      videos:[],
      playing:0,
      watched:false,
      downloads:[]
    };

  };
   componentWillMount(){

      var id= parseInt(this.props.match.params.id);
      var material=[];
       var extract=[];
      try{
       material= this.state.data.find(({Week}) => Week === id);
       extract =material.Videos;
      this.setState({videos:extract});
      this.setState({downloads:material});
      var images = extract.map(x => youtubeLinkParser(x));
      images= images.map(x=> `https://img.youtube.com/vi/${x}/0.jpg`);
      this.setState({thumbnail:images})
     }
     catch(e){
      var getList=JSON.parse(localStorage.getItem("posts")).posts;
      getList =getList.videos;
      material= getList.find(({Week}) => Week === id);
       extract =material.Videos; 
      this.setState({videos:extract});
      this.setState({downloads:material});
      var images = extract.map(x => youtubeLinkParser(x));
      images= images.map(x=> `https://img.youtube.com/vi/${x}/0.jpg`);
      localStorage.setItem("watched", 0);   
      this.setState({thumbnail:images})
     }
  
   }
   
   handleClick  = value => () => {
      var play=this.state.thumbnail.indexOf(value);
      var week=this.state.downloads.Week;
       var completed= play+1;
      var currentWeek =parseInt(localStorage.getItem("current"),10);
       if(currentWeek===week){
      if (completed === this.state.thumbnail.length ){
        localStorage.setItem("Videos",true)
         if(parseInt(localStorage.getItem("watched"),10) ===0){
             var update =parseInt(localStorage.getItem("percent"),10)+34;
             localStorage.setItem("watched", 1);
             localStorage.setItem("percent", update);
           
         }
        }
      var  check =parseInt(localStorage.getItem("percent"),10);
    if(check===100){
              var current =parseInt(localStorage.getItem("current"),10)+1;
             localStorage.setItem("current", current);
             localStorage.setItem("percent", 0);
               localStorage.setItem("watched", 0);
              localStorage.setItem("taskdown",0);
              localStorage.setItem("notedown",0);

             localStorage.setItem("Notes", false);
             localStorage.setItem("Task", false);
             localStorage.setItem("Videos", false);
                           UpdateWeek();

       }


      }

      this.setState({playing:play})
   }
  render(){
    return(
            <div class='startcontent'>
               <div class='Contentcard'>
                    <Media>
                        <div class='video'>
                            <div
                            className={'media-player'}
                            tabIndex="0"
                          >
                              <Player
                                src={this.state.videos[this.state.playing]}
                                class='videoplayer'
                              />
                              <div className="media-controls">
                                <PlayPause className="media-control media-control--play-pause" />
                                <CurrentTime className="media-control media-control--current-time" />
                                <div className="media-control-group media-control-group--seek">
                                  <SeekBar className="media-control media-control--seekbar" />
                                </div>
                                <Duration className="media-control media-control--duration" />
                                <MuteUnmute className="media-control media-control--mute-unmute" />
                                <Volume className="media-control media-control--volume" />
                                <Fullscreen className="media-control media-control--fullscreen" />
                              </div>
                            </div>
                      </div>
                    </Media>
                    <div>
                       <div class='thumb'>
                       <div class='nextheader'>
                        <p style={{'font-size': '1.4em'}}>Next Up</p>
                       </div>
                         <Divider />
                       <div class='nextvids'>
                         <List>
                            {this.state.thumbnail.map((item)=> (
                              <div class='placethumb'>
                                <ListItem button onClick={this.handleClick(item)} >
                                  { item === this.state.thumbnail[this.state.playing] &&(
                                    <PlayArrowIcon />
                                  )}
                                    <img alt='GR' style={{width:'60%',marginLeft:"10px"}}src={item} />
                                </ListItem>
                                  <Divider />
                                </div>
                              ))}
                         </List>
                        </div>
                      </div>

                    </div>
                    <Downloads data={this.state.downloads} />
               </div>
            </div>

    )

  }
}

