 function StartUp(props){
  const classes = useStyles();
  const [expandedA, setExpandedA] = React.useState(false);
  const [selectedWeek, setWeek] = React.useState(null);
  
  const handleExpandClick = key => () =>{
        setWeek(key)
        setExpandedA(!expandedA);
   
    
  };
   var current=parseInt(localStorage.getItem("current"),10);
 
   const data=props.data;
  return (
    <div>
     <Card className={classes.root}>
        <CardHeader
           title="Start Up"
       
        />
        <CardContent>

        <List>

        {data.map(({Week,Focus})=> (
           <ListItem style={{paddingTop:"1vh"}}>
              <Card className={classes.board}>
                <CardHeader
                  title={
                    <Link to={Week <=current ?`/Startup/${Week}` : '#'} style={{ textDecoration: 'none'}}>
                    <p style={{'color':'#000'}}>Week {Week}: {Focus}</p>
                    </Link>
                    }
                       action={
                     <IconButton label="1" onClick={handleExpandClick(Week)}  >
                       <CircularProgressWithLabel  value={Week <current ? 100: Week === current ? localStorage.getItem("percent"):0} />
                     </IconButton>
                     }
                  />
                { selectedWeek ===Week &&(
                   <Collapse in={expandedA} timeout="auto" unmountOnExit>
                <CardContent>
                  <div className="reverse">
                          <Checkbox checked={Week>current ? false : Week <current ? true: Week === current? JSON.parse(localStorage.getItem("Videos")):"false"} value="checkedA" size="small" inputProps={{ 'aria-label': 'Checkbox A' }}/>
                          <Typography style={{'font-size': '15px',marginTop:8,height:6,paddingLeft:15}}>Video Watched</Typography>
                  </div>
                  <div className="reverse">
                          <Checkbox checked={Week>current ? false : Week <current ? true: Week === current? JSON.parse(localStorage.getItem("Notes")):"false"}  value="checkedB" size="small" inputProps={{ 'aria-label': 'Checkbox B' }}/>
                          <Typography style={{'font-size': '15px',marginTop:8,height:6,paddingLeft:15}}>Notes Downloaded</Typography>
                  </div>
                   <div className="reverse">
                          <Checkbox checked={Week>current ? false : Week <current ? true: Week === current? JSON.parse(localStorage.getItem("Task")):"false"}  value="checkedC" size="small" inputProps={{ 'aria-label': 'Checkbox C' }}/>
                          <Typography style={{'font-size': '15px',marginTop:8,paddingLeft:15}}>Task</Typography>
                  </div>
                </CardContent>
                </Collapse>
              )}
              </Card>
              
           </ListItem>
           ))}

          </List>

        </CardContent>

      </Card>
  </div>
  );
}

